  .App {
    text-align: center;
  }

  .App-logo {
    height: 40vmin;
  }

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .App-link {
    color: #09d3ac;
  }
  /* landing page */
  body {
    background: url(https://images.unsplash.com/photo-1521941651707-748bdbae77e9?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80);
    background-size: cover;
    color: black;
    font-size: 18px;
    background-position: center;
    font-family: 'Lato', sans-serif;

  }
  hr {
    width: 400px;
    border-top: 1px solid #f8f8f8;
    border-bottom: 1px solid rgba(0,0,0,0);
  }

  html {
    height: 100%;
  }
  h1 .athlete-card-name{
    font-weight: 700;
    font-size: 3em;
  }
  #content {
    text-align: center;
    padding-top: 25%;

  }
.card-list {
  width:75vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr ;
  grid-gap: 30%;
}

/* card page */

img {
  border-radius: 5%;
}

.card-container {
  display: flex;
  flex-direction: column;
  background-color: #676767;
  border: 2px solid red;
  border-radius: 20px;
  padding: 5%;

  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;

}

.card-container:hover {
  transform: scale(1.05);
}

#dropdown-item-button {

}
.Card-list {
  margin-top: 10%;
}

.card-list-title {
  margin-bottom: 6%;
}

/* profile detail  */

.bg-light {
    background-color: lightgrey;
}

